import { NavController } from '@ionic/angular';

import { Component, OnInit } from '@angular/core';
import { HeaderTitleService } from 'src/app/services/header-title.service';
import { ActivatedRoute} from '@angular/router';

import { LoaderService } from 'src/app/services/loader.service';
import { AuthService } from 'src/app/services/auth.service';
import { ApiProgramsService } from 'src/app/services/api-programs.service';
import { environment } from 'src/environments/environment.prod';
import { ApiGeneralService } from 'src/app/services/api-general.service';

@Component({
  selector: 'app-university-gallery',
  templateUrl: './university-gallery.page.html',
  styleUrls: ['./university-gallery.page.scss'],
})
export class UniversityGalleryPage implements OnInit {

  universityMainID : number = 0;
  universityID : number = 0;
  arrGallery = [];
  webAssetsPath = "";
  constructor(
    private headerTitle : HeaderTitleService,
    private route: ActivatedRoute,
    private apiProgramsService : ApiProgramsService, 
    private auth : AuthService, 
    private loader : LoaderService,
    private navCtrl : NavController,
    private apiGeneralService : ApiGeneralService,
  ) { 
      this.headerTitle.title = "University Gallery";  
      this.headerTitle.isBack = true;
  }

  ngOnInit() {    
    this.universityMainID =  parseInt(this.route.snapshot.params['universityMainID']);
    this.universityID = parseInt(this.route.snapshot.params['universityID']);    
    this.getGalllery();
  }  

  getGalllery(){
    this.webAssetsPath = environment.webAssetsPath + "university/gallery";
    this.loader.presentLoading();
    var uniID : number = this.universityMainID as number;
    let postData = '{"universityMainId": '+uniID+',"IsActive":true}'
    this.apiProgramsService.getGallery(postData).then(resp => {
      console.log(resp);
      this.arrGallery  = resp;
      setTimeout(() => {
        this.loader.loadingDismiss();
      }, 100); 
    })
    .catch(err => {
      console.log("getGallery: " + err);
      if (err.status == "401") {
        this.auth.logout();
      }
      this.loader.loadingDismiss();
    })
  }

  goBack() {
    this.navCtrl.back();
    }

}
